
.buttons {
  overflow: hidden;
  width: 315px;
  margin: 0 auto;
  padding: 30px 0;
}
.buttons .item {
    width: 33.33333%;
    float: left;
    text-align: center;
}
.buttons button {
    cursor: pointer;
    outline: none;
    -ms-progress-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    padding: 10px;
    line-height: 1;
    font-size: 24px;
    font-weight: 700;
    border-radius: 50%;
}
.buttons button:hover {
      opacity: .7;
      transition: .3s;
}
.buttons button:disabled:hover {
      opacity: 1;
}
.buttons .right {
    background-color: #e20000;
    border: 2px solid #af0202;
    color: #fff;
}
.buttons .right:disabled {
      background-color: #af0202;
      border: 2px solid #6d0000;
      color: #6d0000;
}
.buttons .left {
    background-color: #fff;
    border: 2px solid #afafaf;
    color: #000;
}
.buttons .left:disabled {
      background-color: #afafaf;
      border: 2px solid #8e8e8e;
      color: #8e8e8e;
}
.buttons .center {
    background-color: #ec7373;
    border: 2px solid #bf5e5e;
    color: #fff;
}
.buttons .center:disabled {
      background-color: #ad5757;
      border: 2px solid #8e4646;
      color: #793d3d;
}



